@import url("https://fonts.googleapis.com/css2?family=Inter:wght@400;900&display=swap");
@font-face {
  font-family: "display";
  src: url(../fonts/sfDisplay.OTF);
}
@font-face {
  font-family: "displayR";
  src: url(../fonts/sfDisplayR.OTF);
}
*,
:after,
:before {
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 62.5%;
  scroll-behavior: smooth;
}

body {
  background-color: #151515;
}
body p, body a {
  font-size: 1.6rem;
  color: #FAF9F9;
  font-family: "display";
}
@media screen and (min-width: 1024px) {
  body p, body a {
    font-size: 2rem;
  }
}
body img {
  max-width: 100%;
  display: block;
}
body p {
  margin: 0;
  line-height: 1.5;
}
body a {
  text-decoration: none;
}
body ul {
  margin: 0;
  padding: 0;
  list-style: none;
}
body h1, body h2, body h3 {
  font-family: "Inter", sans-serif;
  margin: 0;
}
body h1 {
  font-size: 4.8rem;
  color: #FAF9F9;
}
@media screen and (min-width: 1024px) {
  body h1 {
    font-size: 6.4rem;
  }
}
body h2 {
  font-size: 3.2rem;
  color: #FAF9F9;
}
@media screen and (min-width: 1024px) {
  body h2 {
    font-size: 4rem;
  }
}
body h3 {
  font-size: 2.2rem;
  color: #FAF9F9;
}
@media screen and (min-width: 1024px) {
  body h3 {
    font-size: 2.4rem;
  }
}
body iframe {
  width: 100%;
}

.contenedor {
  max-width: 75rem;
  width: 85%;
  margin-left: auto;
  margin-right: auto;
}

[class$=__heading] {
  position: relative;
  display: inline-block;
  padding-bottom: 2rem;
}
[class$=__heading]:after {
  content: "";
  width: 100%;
  height: 0.5rem;
  display: block;
  background-color: #FF537C;
  position: absolute;
}

