.navegacion {
  position: sticky;
  z-index: 2;
  top: 4rem;
  transform: translateY(-3.5rem);
}
.navegacion--top {
  top: 0;
  transform: translateY(1rem);
}
.navegacion__logo {
  font-size: 2rem;
}
.navegacion__contenedor {
  padding: 2rem;
  position: absolute;
  margin: 0 auto;
  left: 0;
  right: 0;
  max-width: 100rem;
  width: 95%;
  background-color: #FAF9F9;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border: 1px solid #151515;
  border-radius: 2rem;
  box-shadow: 5px 5px 0 black;
}
.navegacion__items {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 2rem;
  background-color: #FAF9F9;
  position: absolute;
  right: 0;
  top: 8.2rem;
  border-radius: 1.5rem;
  padding: 2rem;
  border: 1px solid #151515;
  opacity: 0;
  visibility: hidden;
  transition-property: opacity, visibility, box-shadow;
  transition-duration: 0.3s;
  transition-timing-function: ease;
}
@media screen and (min-width: 1024px) {
  .navegacion__items {
    flex-direction: inherit;
    opacity: 1;
    visibility: visible;
    position: relative;
    top: 0;
    border-radius: 0;
    padding: 0;
    border: 0;
  }
}
.navegacion__items--active {
  top: 8rem;
  box-shadow: 5px 5px 0 black;
  opacity: 1;
  visibility: visible;
}
.navegacion__item {
  transition: text-decoration 0.3s ease;
}
.navegacion__item:hover {
  text-decoration: underline;
}
.navegacion__li--flex {
  display: flex;
  align-items: center;
  gap: 0.3rem;
}
.navegacion__menu {
  display: block;
}
@media screen and (min-width: 1024px) {
  .navegacion__menu {
    display: none;
  }
}
.navegacion__logo, .navegacion__item, .navegacion__menu {
  color: #151515;
}

